export default {
  props: ["data"],
  methods: {
    signup: function signup() {
      this.data.isShow = false;
      this.$emit('signup');
    },
    submitForm: function submitForm() {
      var _this = this;

      if (!this.data.imgUrl) {
        this.$message.error("Please select a picture !");
        return false;
      }

      if (!this.data.detail) {
        this.$message.error("Please fill in the product URL !");
        return false;
      }

      var reg = /^\w+((.\w+)|(-\w+))@[A-Za-z0-9]+((.|-)[A-Za-z0-9]+).[A-Za-z0-9]+$/;

      if (!this.data.email || !reg.test(this.data.email)) {
        this.$message.error("Please fill in the correct Email !");
        return false;
      }

      this.data.loading = true;
      this.$apiCall("api.OfferList.genOfferProduct", {
        products: [{
          imgUrl: this.data.imgUrl,
          detail: this.data.detail,
          email: this.data.email,
          otherContact: this.data.otherContact
        }]
      }, function (r) {
        _this.data.loading = false;

        if (r.ErrorCode == 9999) {
          _this.data.type = 3;

          _this.$message.success("Submit Success !");

          var bindOfferIds = JSON.parse(localStorage.getItem('c_homeBindOfferIds')) || [];
          bindOfferIds.push(r.Data.Results);
          localStorage.setItem('c_homeBindOfferIds', JSON.stringify(_toConsumableArray(new Set(bindOfferIds))));
        } else {
          _this.$message.error(r.Message);
        }
      });
    },
    delImg: function delImg() {
      this.data.imgUrl = '';
    },
    uploadImgBase64: function uploadImgBase64(base64, ext) {
      var _this2 = this;

      this.data.loading = true;
      this.$apiCall("api.User.uploadImgBase64", {
        imgUrlBase64: base64,
        ext: ext
      }, function (r) {
        _this2.data.loading = false;

        if (r.ErrorCode == 9999) {
          _this2.data.imgUrl = r.Data.Results.imgUrl;
        } else {
          _this2.$message.error(r.Message);
        }
      });
    },
    Base64: function Base64(el) {
      var _this3 = this;

      if (el.target.value) {
        var reader = new FileReader(),
            img = new Image(),
            ext = "";

        reader.onload = function (e) {
          img.src = e.target.result;
          var extArr = e.target.result.match(/data:image\/(.*);base64,.*/);
          ext = extArr[1];
        };

        img.onload = function (e) {
          base64Image({
            file: el.target,
            callback: function callback(rst) {
              var base64 = rst.split("base64,").pop();

              _this3.uploadImgBase64(base64, ext);
            }
          });
        };

        reader.readAsDataURL(el.target.files[0]);
      }
    },
    openUpload: function openUpload() {
      //打开上传
      document.getElementById('pfUpload').click();
    },
    openChat: function openChat() {// Tawk_API.maximize();
    },
    changeType: function changeType(type) {
      this.data.type = type;
    },
    onCopy: function onCopy(e) {
      this.$elementMessage('Has been copied to the clipboard', 'success');
    },
    onError: function onError(e) {
      this.$elementMessage('Copy to clipboard failed, please copy manually', 'error');
    }
  }
};